import calendar from "@/components/uni-calendar/uni-calendar"
export default{
  components: {
    calendar
  },
  data() {
    return {
      code: 145,
      dayNum: 9,
      btnValue: '签到',
      infoShow: false,
      successAlert: false
    }
  },
  computed: {
    date() {
      let time = new Date()
      return time.getFullYear() + '-' + (time.getMonth() + 1 ) + '-' + time.getDate()
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    signAction() {
      if(this.btnValue === '已签到') {
        return
      }
      this.btnValue = '已签到'
      this.infoShow = true
      this.successAlert = true
      this.dayNum += 1
      let that = this
      let i = 1;
      // 连续加动态效果
      setInterval(function(){
        if(i>30) {
          return
        }
        that.code += 1
        i++
      }, 30)
      
    },
    change(e) {
      console.log('改变了',e);
    },
    toClick(e) {
      console.log('点击了',e);
    }
  }
}
